.toggler {
  display: block;
  border: none;
  padding: 0;
  background-color: rgba(255, 255, 255, 0);
  cursor: pointer;
  position: relative;
  z-index: 101;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 32px;
  border-radius: 6px;
  background-color: rgba(255, 255, 255, 0);
  cursor: pointer;
  -webkit-tap-highlight-color: transparent;
}
.toggler span {
  position: relative;
  display: block;
  width: 30px;
  height: 2px;
  border-radius: 2px;
  background-color: #ffffff;
  transition: transform 0.2s ease, background 0.2s ease;
}
.toggler span::before, .toggler span::after {
  content: "";
  display: block;
  position: absolute;
  left: 0;
  width: 100%;
  height: 2px;
  border-radius: 2px;
  background-color: #ffffff;
  transition: transform 0.2s ease;
}
.toggler span::before {
  animation: toggler-top 0.4s ease-in forwards;
}
.toggler span::after {
  animation: toggler-bottom 0.4s ease-in forwards;
}
@media (min-width: 1024px) {
  .toggler {
    display: none;
  }
}
.toggler--close span {
  background-color: transparent;
}
.toggler--close span::before {
  animation: toggler-top-active 0.4s ease-in forwards;
}
.toggler--close span::after {
  animation: toggler-bottom-active 0.4s ease-in forwards;
}

@keyframes toggler-top {
  0% {
    transform: translateY(0) rotate(45deg);
  }
  50% {
    transform: translateY(0) rotate(0);
  }
  100% {
    transform: translateY(-8px);
  }
}
@keyframes toggler-bottom {
  0% {
    transform: translateY(0) rotate(-45deg);
  }
  50% {
    transform: translateY(0) rotate(0);
  }
  100% {
    transform: translateY(8px);
  }
}
@keyframes toggler-top-active {
  0% {
    transform: translateY(-8px);
  }
  50% {
    transform: translateY(0);
  }
  100% {
    transform: translateY(0) rotate(45deg);
  }
}
@keyframes toggler-bottom-active {
  0% {
    transform: translateY(8px);
  }
  50% {
    transform: translateY(0);
  }
  100% {
    transform: translateY(0) rotate(-45deg);
  }
}
